import Swal from 'sweetalert2';

import { types } from "../types/types";
import { fetchConToken, fetchSinToken } from "../helpers/fetch"
import { startLoadProducts } from './products';
import { startLoadCategories } from './categories';
import { startLoadUsers } from './users';


export const StartLogin = (email, password) => {
    return async( dispatch ) => {   


        try {
            const resp = await fetchSinToken( 'auth/login', { email, password }, 'POST' );
            const body = await resp.json();


            if( body.token ) {    

                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());


                dispatch( login({
                    uid: body.user._id,
                    name: body.user.name 
                }) );
                
            } else {
                return Swal.fire('Error', body.msg, 'error');
            }
            

        } catch (error) {
            return Swal.fire('Error', error, 'error');
        }
    }
} 

const login = ( user ) => ({
    type: types.authLogin,
    payload: user
})

export const startRegister = ( name, email, password ) => {
    return async( dispatch ) => {


        try {
            const resp = await fetchSinToken( 'users', { name, email, password }, 'POST' );
            const body = await resp.json();

            console.log(body);

            if( body.token ) {    

                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());


                dispatch( login({
                    uid: body.user._id,
                    name: body.user.name 
                }) );
            } else {
                return Swal.fire('Error', body.msg, 'error');
            }
            

        } catch (error) {
            return Swal.fire('Error', error, 'error');
        }
    }

}

export const startChecking = () => {
    return async( dispatch ) => {

        try {
            const resp = await fetchConToken('auth/renew');
            
            
            if( resp !== undefined) {
                
                const body = await resp.json();
                
                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());
                
    
                dispatch( startLoadProducts() )
                dispatch( startLoadCategories() )
                dispatch( startLoadUsers() )
                
    
                dispatch( login({
                    uid: body.uid,
                    name: body.name 
                }) );
            } else {
                dispatch( checkingFinish() );
            }
            
        } catch (error) {
            console.log(error);
        }

    }
}

const checkingFinish = () => ({
    type: types.authCheckingFinish
})

export const startLogout = () => {
    return ( dispatch ) => {
        
        localStorage.clear();
    
        dispatch( logout() )
    }
}

const logout = () => ({ type: types.authLogout })

export const startGoogleLogin = (id_token) => {
    return async( dispatch ) => {   


        try {
            const resp = await fetchSinToken( 'auth/google', {id_token}, 'POST' );
            const body = await resp.json();

            if( body.token ) {    

                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());


                dispatch( login({
                    uid: body.user._id,
                    name: body.user.name 
                }) );
                
            } else {
                return Swal.fire('Error', body.msg, 'error');
            }
            

        } catch (error) {
            return Swal.fire('Error', error, 'error');
        }
    }

}
