import * as React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    matchPath,
    useLocation,
    useNavigate
} from "react-router-dom";

import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MailIcon from '@mui/icons-material/Mail';
import InventoryIcon from '@mui/icons-material/Inventory';
import GroupIcon from '@mui/icons-material/Group';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AutoDeleteIcon from '@mui/icons-material/AutoDelete';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import ArrowCircleUpIcon from '@mui/icons-material/ArrowCircleUp';
import { uiOpenProductModal, uiOpenProductModalEdit, uiOpenUserModalAdd, uiOpenUserModalEdit, uiOpenDialogDelete } from '../../../actions/ui';
import { userStartDeleted } from '../../../actions/users';
import { productStartDeleted } from '../../../actions/products';
import { productBinStartDeleted, productBinStartEnable, userBinStartDeleted, userBinStartEnable } from '../../../actions/Bin';

const drawerWidth = 240;

const openedMixin = (theme) => ({
    width: drawerWidth,
    transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
    }),
    overflowX: 'hidden',
});

const closedMixin = (theme) => ({
    transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: `calc(${theme.spacing(7)} + 1px)`,
    [theme.breakpoints.up('sm')]: {
        width: `calc(${theme.spacing(9)} + 1px)`,
    },
});

const DrawerHeader = styled('div')(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap',
        boxSizing: 'border-box',
        ...(open && {
            ...openedMixin(theme),
            '& .MuiDrawer-paper': openedMixin(theme),
        }),
        ...(!open && {
            ...closedMixin(theme),
            '& .MuiDrawer-paper': closedMixin(theme),
        }),
    }),
);

export const NavigationMenu = () => {


    const theme = useTheme();

    const navigate = useNavigate();
    const { pathname } = useLocation();

    const dispatch = useDispatch();

    const { activeUser } = useSelector(state => state.users);
    const { activeProduct } = useSelector(state => state.product);

    const { activeBinUser } = useSelector(state => state.bin);
    const { activeBinProduct } = useSelector(state => state.bin);





    const handleAddButton = () => {

        switch (pathname) {
            case '/dashboard/products':
                dispatch(uiOpenProductModal())
                break;

            case '/dashboard/users':
                dispatch(uiOpenUserModalAdd())
                break;


            default:
                break;
        }

    }
    const handleEditButton = () => {

        switch (pathname) {
            case '/dashboard/products':
                dispatch(uiOpenProductModalEdit())
                break;

            case '/dashboard/users':
                dispatch(uiOpenUserModalEdit())
                break;


            default:
                break;
        }

    }
    const handleDeleteButton = () => {

        switch (pathname) {

            case '/dashboard/products':
                dispatch(uiOpenDialogDelete())
                break;

            case '/dashboard/users':
                dispatch(uiOpenDialogDelete())
                break;


            default:
                break;
        }

    }



    const handleBinEnable = () => {
        switch (pathname) {

            case '/dashboard/bin/products':
                dispatch(productBinStartEnable(activeBinProduct))
                break;

            case '/dashboard/bin/users':
                dispatch(userBinStartEnable(activeBinUser))
                break;


            default:
                break;
        }
    }

    const handleBinDelete = () => {
        switch (pathname) {

            case '/dashboard/bin/products':
                dispatch(uiOpenDialogDelete())
                break;

            case '/dashboard/bin/users':
                dispatch(uiOpenDialogDelete())
                break;


            default:
                break;
        }
    }


    const [anchorEl, setAnchorEl] = React.useState(null);
    const handleOpenMenuBin = Boolean(anchorEl);

    const handleClick = (event) => {

        if (!!anchorEl) {
            setAnchorEl(null);
        } else {
            setAnchorEl(event.currentTarget);
        }
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <>

            <Box sx={{ display: 'flex' }}>
                <CssBaseline />
                <AppBar position="fixed" open={open}>

                    <Toolbar>


                                <IconButton
                                    color="inherit"
                                    aria-label="open drawer"
                                    onClick={handleDrawerOpen}
                                    edge="start"
                                    sx={{
                                        marginRight: '36px',
                                        ...(open && { display: 'none' }),
                                    }}
                                >
                                    <MenuIcon />

                                </IconButton>

                                <Typography
                                    sx={{
                                        'cursor': 'pointer',
                                    }}
                                    onClick={() => navigate('/dashboard', { replace: true })}
                                    variant="h6"
                                    noWrap component="div"
                                >
                                    Panel de control
                                </Typography>


                    </Toolbar>


                </AppBar>

                    <Drawer variant="permanent" open={open}>

                        <DrawerHeader>
                            <IconButton onClick={handleDrawerClose}>
                                {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
                            </IconButton>
                        </DrawerHeader>

                        <Divider />

                        <List>

                            <ListItem button onClick={() => navigate('/dashboard/products', { replace: true })}>

                                <ListItemIcon>
                                    <InventoryIcon />
                                </ListItemIcon>

                                <ListItemText primary="Products" />

                            </ListItem>

                            <ListItem button onClick={() => navigate('/dashboard/users', { replace: true })}>
                                <ListItemIcon>
                                    <GroupIcon />
                                </ListItemIcon>

                                <ListItemText primary="Users" />

                            </ListItem>


                            <ListItem button onClick={() => navigate('/dashboard/regist', { replace: true })}>
                                <ListItemIcon>
                                    <LibraryBooksIcon />
                                </ListItemIcon>

                                <ListItemText primary="Regist" />

                            </ListItem>


                            <ListItem button onClick={handleClick}>
                                <ListItemIcon>
                                    <AutoDeleteIcon />
                                </ListItemIcon>

                                <ListItemText primary="Bin" />

                                <Menu
                                    id="basic-menu"
                                    anchorEl={anchorEl}
                                    open={handleOpenMenuBin}
                                    onClose={handleClose}
                                    MenuListProps={{
                                        'aria-labelledby': 'basic-button',
                                    }}
                                >
                                    <MenuItem onClick={() => navigate('/dashboard/bin/products', { replace: true })} >Products</MenuItem>
                                    <MenuItem onClick={() => navigate('/dashboard/bin/users', { replace: true })} >Users</MenuItem>
                                </Menu>

                            </ListItem>

                        </List>
                        <Divider />

                        {
                            (pathname === '/dashboard/products' || pathname === '/dashboard/users')
                            &&
                            <List>

                                <ListItem button onClick={handleAddButton}>
                                    <ListItemIcon>
                                        <AddIcon />
                                    </ListItemIcon>

                                    <ListItemText primary="Add new" />

                                </ListItem>

                                {
                                    (!!activeUser || !!activeProduct)
                                    &&
                                    <>
                                        <ListItem button onClick={handleEditButton}>
                                            <ListItemIcon>
                                                <EditIcon />
                                            </ListItemIcon>

                                            <ListItemText primary="Edit" />

                                        </ListItem>

                                        <ListItem button onClick={handleDeleteButton}>
                                            <ListItemIcon>
                                                <DeleteIcon />
                                            </ListItemIcon>

                                            <ListItemText primary="Delete" />

                                        </ListItem>
                                    </>
                                }
                            </List>
                        }

                        {
                            (pathname === '/dashboard/bin/products' || pathname === '/dashboard/bin/users')
                            &&
                            <>
                                {
                                    (!!activeBinProduct || !!activeBinUser)
                                    &&
                                    <List>
                                        <ListItem button onClick={handleBinEnable}>
                                            <ListItemIcon>
                                                <ArrowCircleUpIcon />
                                            </ListItemIcon>

                                            <ListItemText primary="Enable" />

                                        </ListItem>

                                        <ListItem button onClick={handleBinDelete}>
                                            <ListItemIcon>
                                                <DeleteForeverIcon />
                                            </ListItemIcon>

                                            <ListItemText primary="Delete forever" />

                                        </ListItem>
                                    </List>
                                }
                            </>
                        }

                    </Drawer>
            </Box>
        </>
    );
}
